import * as React from 'react';
import Stack from '@mui/material/Stack';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert, { AlertProps } from '@mui/material/Alert';
import {useSelector} from 'react-redux';
import {AppRootStateType} from '../../../utils/reduxUtils.types';
import {appActions} from '../../../features/Common/Actions/AppActions';
import {useActions} from "../../../utils/reduxUtils";

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(
    props,
    ref,
) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

export const ErrorSnackBar = () => {
    const error = useSelector<AppRootStateType, string | null>(state => state.app.error);
    const {setAppError} = useActions(appActions);

    const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
        if (reason === 'clickaway') {
            return
        }
        setAppError({error: null});
    };

    const isOpen = error !== null

    return (
        <Stack spacing={2} sx={{ width: '100%' }}>
            <Snackbar
                open={isOpen}
                autoHideDuration={3000}
                onClose={handleClose} sx={{ left: '50% !important', transform: 'translateX(-50%)' }}
            >
                <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }} >
                    {error}
                </Alert>
            </Snackbar>
        </Stack>
    );
}
